// @flow

import React from 'react'
import {graphql} from 'gatsby'
import {I18n} from 'aws-amplify'

import Main from '../components/main'

import type {MainProps} from '../components/main'

const Index = (props: MainProps) => {
  const {
    data: {
      site: {
        siteMetadata: {
          author: {summary: authorSummary},
          social: {email, telegram, github, youtube},
        },
      },
      allMarkdownRemark: {edges: posts},
    },
    location,
  } = props

  return (
    <Main
      title={I18n.get('fusion360')}
      authorSummary={authorSummary}
      email={email}
      telegram={telegram}
      github={github}
      youtube={youtube}
      posts={posts}
      location={location}
    />
  )
}

export default Index

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        author {
          summary
        }
        social {
          github
          email
          youtube
          telegram
        }
      }
    }
    allMarkdownRemark(
      sort: {fields: [frontmatter___date], order: DESC}
      limit: 13
      filter: {frontmatter: {tags: {in: "fusion-360"}}}
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date
            title
            coverImage {
              src
              owner {
                name
                link
              }
              platform {
                name
                link
              }
            }
          }
        }
      }
    }
  }
`
