// @flow

import React from 'react'
import {
  Typography,
  Grid,
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Hidden,
} from '@material-ui/core'
import styled from 'styled-components'
import {I18n} from 'aws-amplify'
import {Link} from 'gatsby'
import moment from 'moment'

type Props = {|
  post: {
    excerpt: string,
    frontmatter: {|
      title: string,
      date: string,
      coverImage: {|
        src: string,
        owner: {|
          name: string,
          link: string,
        |},
        platform: {|
          name: string,
          link: string,
        |},
      |},
    |},
    fields: {|
      slug: string,
    |},
  },
|}

const MyCard = styled(Card)`
  display: flex;
`

const CardDetails = styled.div`
  flex: 1;
`

const MyCardMedia = styled(CardMedia)`
  width: 160px;
`

export default function FeaturedPost(props: Props) {
  const {
    post: {
      excerpt,
      frontmatter: {
        date,
        title,
        coverImage: {
          src,
          owner: {name: coverImageOwnerName},
          platform: {name: coverImagePlatformName},
        },
      },
      fields: {slug},
    },
  } = props

  return (
    <Grid item xs={12} md={6}>
      <CardActionArea component={Link} to={slug}>
        <MyCard>
          <CardDetails>
            <CardContent>
              <Typography component="h2" variant="h5">
                {title}
              </Typography>
              <Typography variant="subtitle1" color="textSecondary">
                {moment(date)
                  .local()
                  .format('YYYY-MM-DD A hh:mm:ss')}
              </Typography>
              <Typography variant="subtitle1" paragraph>
                {excerpt.length > 65 ? `${excerpt.substring(0, 65)}...` : excerpt}
              </Typography>
              <Typography variant="subtitle1" color="primary">
                {I18n.get('continueReading')}
              </Typography>
            </CardContent>
          </CardDetails>
          <Hidden xsDown>
            <MyCardMedia
              image={src}
              title={`Photo by ${coverImageOwnerName} on ${coverImagePlatformName}`}
            />
          </Hidden>
        </MyCard>
      </CardActionArea>
    </Grid>
  )
}
