// @flow

import React from 'react'
import {I18n} from 'aws-amplify'
import {Grid, Paper, Typography} from '@material-ui/core'
import {GitHub, Email, Telegram, YouTube} from '@material-ui/icons'
import {OutboundLink} from 'gatsby-plugin-google-gtag'

import styled from 'styled-components'

type Props = {
  description: string,
  github: string,
  telegram: string,
  email: string,
  youtube: string,
}

const MyPaper = styled(Paper)`
  padding: ${props => props.theme.spacing(2)}px;
  background-color: ${props => props.theme.palette.grey[200]};
`

const MyTypography = styled(Typography)`
  margin-top: ${props => props.theme.spacing(3)}px;
`

const MyLink = styled(OutboundLink)`
  text-decoration: none;
  color: ${props => props.theme.palette.primary.main};
`

export default function Sidebar(props: Props) {
  const {description, email, telegram, youtube, github} = props

  return (
    <Grid item xs={12} md={4}>
      <MyPaper elevation={0}>
        <Typography variant="h6" gutterBottom>
          {I18n.get('about')}
        </Typography>
        <Typography variant="body2">{description}</Typography>
      </MyPaper>
      <MyTypography variant="h6" gutterBottom>
        SNS
      </MyTypography>

      <MyLink href={`https://t.me/${telegram}`} key="telegram">
        <Grid container direction="row" spacing={1} alignItems="center">
          <Grid item>
            <Telegram />
          </Grid>
          <Grid item>Telegram</Grid>
        </Grid>
      </MyLink>

      <MyLink href={`https://github.com/${github}`} key="github">
        <Grid container direction="row" spacing={1} alignItems="center">
          <Grid item>
            <GitHub />
          </Grid>
          <Grid item>GitHub</Grid>
        </Grid>
      </MyLink>

      <MyLink href={`https://youtube.com/c/${youtube}`} key="youtube">
        <Grid container direction="row" spacing={1} alignItems="center">
          <Grid item>
            <YouTube />
          </Grid>
          <Grid item>YouTube</Grid>
        </Grid>
      </MyLink>

      <MyLink href={`mailto:${email}`} key="email">
        <Grid container direction="row" spacing={1} alignItems="center">
          <Grid item>
            <Email />
          </Grid>
          <Grid item>E-mail</Grid>
        </Grid>
      </MyLink>
    </Grid>
  )
}
