// @flow

import React from 'react'
import {Grid} from '@material-ui/core'
import styled from 'styled-components'
import {I18n} from 'aws-amplify'

import Layout from '../layout'
import SEO from '../seo'

import Sidebar from './sidebar'
import MainFeaturedPost from './main-featured-post'
import FeaturedPost from './featured-post'
import Posts from './posts'

const MainGrid = styled(Grid)`
  margin-top: ${props => props.theme.spacing(3)}px;
`

export type MainProps = {
  data: {|
    site: {|
      siteMetadata: {
        title: string,
        author: {|
          summary: string,
        |},
        social: {|
          github: string,
          telegram: string,
          youtube: string,
          email: string,
        |},
      },
    |},
    allMarkdownRemark: {|
      edges: Array<{|
        node: {|
          frontmatter: {|
            title: string,
            date: string,
            coverImage: {|
              src: string,
              owner: {|
                name: string,
                link: string,
              |},
              platform: {|
                name: string,
                link: string,
              |},
            |},
          |},
          excerpt: string,
          fields: {|
            slug: string,
          |},
        |},
      |}>,
    |},
  |},
  location: {
    pathname: string,
  },
}

type Props = {|
  title: string,
  authorSummary: string,
  email: string,
  telegram: string,
  github: string,
  youtube: string,
  posts: Array<{|
    node: {|
      frontmatter: {|
        title: string,
        date: string,
        coverImage: {|
          src: string,
          owner: {|
            name: string,
            link: string,
          |},
          platform: {|
            name: string,
            link: string,
          |},
        |},
      |},
      excerpt: string,
      fields: {|
        slug: string,
      |},
    |},
  |}>,
  location: {
    pathname: string,
  },
|}

const Main = (props: Props) => {
  const {title, authorSummary, email, telegram, github, youtube, posts, location} = props

  return (
    <Layout location={location}>
      <SEO title={title} />
      <main>
        {posts.length > 0 ? <MainFeaturedPost post={posts[0].node} /> : null}

        <Grid container spacing={4}>
          {posts.slice(1, 3).map(post => {
            const {node} = post
            const {
              fields: {slug},
            } = node

            return <FeaturedPost key={slug} post={node} />
          })}
        </Grid>
      </main>
      <MainGrid container spacing={5}>
        <Posts title={I18n.get('posts')} posts={posts.slice(3)} />
        <Sidebar
          description={authorSummary}
          telegram={telegram}
          youtube={youtube}
          email={email}
          github={github}
        />
      </MainGrid>
    </Layout>
  )
}

export default Main
